.studyViewSummaryHeader .Select {
  font-size: 14px;
  width: 220px;
  height: 30px; }
  .studyViewSummaryHeader .Select .Select-control {
    height: 28px; }
  .studyViewSummaryHeader .Select .Select-input {
    height: 28px; }
    .studyViewSummaryHeader .Select .Select-input input {
      padding: 0px 5px;
      line-height: 28px; }
  .studyViewSummaryHeader .Select .Select-value {
    line-height: 28px !important; }
    .studyViewSummaryHeader .Select .Select-value .Select-value-label {
      line-height: 28px; }
  .studyViewSummaryHeader .Select .Select-menu-outer {
    width: 300px;
    max-height: 400px;
    z-index: 100; }
  .studyViewSummaryHeader .Select .Select-placeholder {
    line-height: 28px; }
  .studyViewSummaryHeader .Select .Select-menu {
    max-height: 398px;
    overflow: hidden; }
    .studyViewSummaryHeader .Select .Select-menu > div > div:nth-child(2) {
      max-height: 350px !important;
      overflow-x: hidden !important;
      overflow-y: scroll !important; }
  .studyViewSummaryHeader .Select .Select-option {
    line-height: 1.25; }

[data-mfb-label]:after {
  background: black !important;
  color: white !important; }

.mfb-component__main-icon--active,
.mfb-component__main-icon--resting,
.mfb-component__child-icon {
  line-height: 56px !important;
  font-size: 18px !important; }

#geneBoxValidationStatus > div,
#wideGeneBoxValidationStatus > div {
  white-space: normal;
  width: 100%; }

#wideGeneBoxValidationStatus > div {
  max-width: 555px; }

.studyFilterResult-enter-done {
  transform: scale(1) !important;
  /* Standard syntax */ }

.studyViewAddChartOverlay.rc-tooltip-placement-bottomRight .rc-tooltip-arrow {
  right: 7% !important; }

.addChartTabs .nav > li > a {
  padding: 5px; }

.addChartTabs .nav .active a {
  padding-top: 5px !important; }

.addChartTabs .tab-content {
  min-height: 100px !important;
  padding: 10px 0 10px 0 !important; }
  .addChartTabs .tab-content .msk-tab {
    padding-left: 10px !important;
    padding-right: 10px !important; }

iframe.mdacc-heatmap-iframe {
  height: calc(100vh - 200px);
  min-height: 700px; }

.resourcesTab .resourcesSection {
